.head-icons {
  font-size: 20px !important;
  margin-top: 18px !important;
}
.head-icons i {
  margin-left: 24px;
  font-size: 20px;
  background-size: 219px;
  background-position: -87px 4px;
  cursor: pointer;
}
@keyframes pulse_animation {
  25% {
    -webkit-transform: scale(1.3);
    transform: scale(1.3);
  }
  75% {
    -webkit-transform: scale(0.8);
    transform: scale(0.8);
  }
}
.head-icons i:hover {
  animation-name: pulse_animation;
  animation-duration: 1000ms;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
}

.header-bottom {
  background: linear-gradient(to left, #fff, #ed1165, #fff) !important;
  height: 1px !important;
  border: 1px solid #f1f1f1 !important  ;
}
.header-appbar {
  background-color: #fff !important;
  text-align: center !important;
  top: 0;
  padding-top: 30px;
  width: 100%;
  line-height: 1.42857143;
  font-size: 12px;
  font-family: sans-serif;
  box-shadow: none !important;
}
.header-navbar-list {
  padding-top: 22px;
  height: 55px !important;
}
.header-navbar-list a {
  white-space: nowrap;
  width: 100%;
  font-size: 14px;
  letter-spacing: 1px;
  text-transform: capitalize;
  font-size: 15px !important;
  text-decoration: none !important;
  margin: 1.5%;
}
.img {
  height: 60px !important;
  width: 100%;
}
.search {
  border: 1px solid #ccc;
  width: 165px !important;
  height: 26px !important;
  padding: 6px 10px !important;
  font-family: inherit !important;
}
.MuiBadge-colorSecondary {
  background-color: #f699a3 !important;
  top: -6px !important;
}
.header-appbar-moblie {
  background-color: #fff !important;
  text-align: center !important;
  top: 0;
  padding: 0 !important;
  position: sticky;
  width: 100%;
  line-height: 1.42857143;
  font-size: 12px;
  font-family: sans-serif;
  box-shadow: none !important;
  box-shadow: 1px 1px 1px #ccc !important;
}
.mobli-icon i {
  font-size: 18px;
  cursor: pointer;
  color: #394578;
  margin-left: 10px;
  border-left: 1px solid #c3c3c3;
  text-align: center !important;
  width: 25px;
}
@keyframes pulse_animation {
  25% {
    -webkit-transform: scale(1.3);
    transform: scale(1.3);
  }
  75% {
    -webkit-transform: scale(0.8);
    transform: scale(0.8);
  }
}
.mobli-icon i:hover {
  animation-name: pulse_animation;
  animation-duration: 1000ms;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
}
.list-items {
  text-align: left;
  white-space: nowrap;
  width: 100%;
  font-size: 14px;
  letter-spacing: 1px;
  color: #394578;
  text-transform: capitalize;
}
.Jew-mbl-head-list {
  color: #394578;
  width: 100%;
  margin: 0;
  float: none;
  font-size: 12px;
  padding: 0 6px;
  position: relative;
  border-left: 3px solid #fff;
  letter-spacing: 1px;
}

.ListColor span:hover div:hover {
  border-left: 3px solid #ed1165;
  color: #ed1165;
}

.ListColor span:active {
  border-left: 3px solid #ed1165;
  color: #ed1165;
}
.hrColor {
  border-bottom: 1px solid #ed1165;
}
